.people {
  color: #2f4f4f;
  box-sizing: border-box;
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 40px 30px;
  padding: 30px 35px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person__photo {
  position: relative;
}

.person__photo > img {
  border-radius: 50%;
  width: 100%;
}

.person__photo-rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  margin-right: 10px;
  font-size: 16px;
  line-height: 200%;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.person__details {
  text-align: center;
  margin-top: 8px;
  position: relative;
}

.person__extra {
  color: #000;
  text-align: center;
  margin-top: 9px;
  font-size: 18px;
  font-weight: 400;
  display: none;
}

.person:hover {
  background-color: #eee;
  border-radius: 10px;
  padding: 10px 10px 16px;
  transform: scale(1.2);
}

.person:hover .person__extra {
  display: block;
}

.person:hover .person__photo > img {
  border-radius: 60px;
}

.person {
  transition: all 1s;
}
